import React, { useEffect, useState } from "react";
import { getFirestore, collection, getDocs, addDoc, where, query, doc, updateDoc } from 'firebase/firestore';
import { getStorage } from "firebase/storage";
import { adminNum } from "./constants";

const FirebaseContext = React.createContext({
    firebaseApp: {},
    firebaseStorage: {},
    fetchAlbum: ()=>{},
    setFirebaseObj: (obj)=>{},
    albums: {"list":[]},
    addAlbum: ()=>{},
    updateAlbum: ()=>{},
    reset: ()=>{},
    isLoading:true,
    setLoading : ()=>{},
    saveEditedAlbum: (obj)=>{}
});

export default FirebaseContext;

let isInitialRun = true;
export const FirebaseAppProvider=(props)=>{
    const [isLoading,setIsLoading] = useState(true);
    const [firebaseApp,setFirebaseApp] = useState({});
    const [db,setDb] = useState();
    const [albums,putAlbums] = useState({"list":[]});
    const [storage,setStorage] = useState({});

    let setFirebaseObj=(async (app)=>{
        console.log("Firebase APP state set in context");
        let db = await getFirestore(app);
        setDb(db);
        setFirebaseApp(app);
        setStorage(getStorage(app,"gs://stushare-webapp.appspot.com"));
    })

    let fetchAlbum=(async()=>{
        console.log("FETCH ALBUM RAN...");
        let q ="";
        if(window.localStorage.getItem("loginPhone")===adminNum)
        {
             q = query(collection(db, "albums"));
        }else{
             q = query(collection(db, "albums"), where("userphone", "array-contains", window.localStorage.getItem("loginPhone")));
        }
        const snapshot = await getDocs(q);
        const list = snapshot.docs.map(doc => {
          let tmp = doc.data();
          tmp.docId = doc.id;
          return(tmp);
        });
        console.log(window.localStorage.getItem("loginPhone")+" Album List Fetched "+ list);
        if(list){
            putAlbums({"list":list});
        }else{
            putAlbums({"list":[]});
        }
        setLoading(false);
    })
    let updateAlbum=async(docObj,userNew)=>{   
      const updateAlbumFb = doc(db, "albums", docObj.docId);
      let userphoneNew = userNew.map((v)=>{return(v.phone);});
      await updateDoc(updateAlbumFb, {userphone:userphoneNew});
      await updateDoc(updateAlbumFb, {users:userNew});
      console.log("Update Done ");
      fetchAlbum();
    }
    let addAlbum=async(obj)=>{
        try {
            console.log(db);
            const docRef = await addDoc(collection(db, "albums"), 
            obj

        // {
        // "id": "1",
        // "studio":"Abcdefgh Studio",
        // "website":"https://example.com/1234",
        // "owner":"9499030204",
        // "name": "First Birthday Event",
        // "date": "Janaury 1,2022",
        // "image": "https://i.ytimg.com/vi/yCtFuVM1b2U/maxresdefault.jpg",
        // "fullDesc": "This is the description of this event. sumary of the event goes here.",
        // "instagram": "https=//www.instagram.com/",
        // "youtube": "https=//www.youtube.com/",
        // "instagramId": "myInstId",
        // "youtubeId": "myChannelName",
        // "images": [
        //   "https://www.commondreams.org/sites/default/files/views-article/thumbs/happy_birthday.jpg",
        //   "https://i.pinimg.com/originals/a0/94/30/a09430348397b97e66d94137c68f3ec5.jpg",
        //   "https://media.weddingz.in/images/3ca38d81285da86dd0dad92d29f46e1a/aura-banquets-aura-banquets-aura-banquet-2.jpg"
        // ],
        // "videos": [
        //   "https://www.learningcontainer.com/wp-content/uploads/2020/05/sample-mp4-file.mp4"
        // ],
        // "userphone":["9499030204","7871485184"],
        // "users": [
        //   {
        //     "phone": "9123211233",
        //     "message": "abcd efgh"
        //   },
        //   {
        //     "phone": "7823439893",
        //     "message": "opiotiurie eu"
        //   },
        //   {
        //     "phone": "849233094",
        //     "message": "ajbsuisuin"
        //   }
        // ]
        //     }
        );
            console.log("Document written with ID: ", docRef.id);
            setLoading(false);
          } catch (e) {
            console.error("Error adding document: ", e);
          }
    }
    let saveEditedAlbum=async(docObj)=>{   
        console.log("docObj",docObj);
      const updateAlbumFb = doc(db, "albums", docObj.docId);
      await updateDoc(updateAlbumFb, docObj);
      console.log("Update Content of Album Done ");
      fetchAlbum();
    }

    let reset=()=>{
        putAlbums({"list":[]});
    }

    let setLoading = (val)=>{
      setIsLoading(val);
    }

    useEffect(()=>{
        if(!isInitialRun){
            console.log("CHANGES HAPPENED in DB");
            fetchAlbum();
            // addAlbum();
        }
        else{isInitialRun=false;}
    },[db]);

    return(<FirebaseContext.Provider value={{
        isLoading:isLoading,
        setLoading: setLoading,
        firebaseApp: firebaseApp,
        fetchAlbum: fetchAlbum,
        setFirebaseObj: setFirebaseObj,
        albums: albums,
        addAlbum:addAlbum,
        updateAlbum:updateAlbum,
        saveEditedAlbum:saveEditedAlbum,
        reset: reset,
        storage:storage
    }} >
        {props.children}
    </FirebaseContext.Provider>);
}